.button {
    display: inline-block;
    margin-top: 20px;
    padding: 10px;
    
    font-size: 20px;
    color: #212121;
    background-color: #f5f4fa;
    text-decoration: none;
    border: 1px solid grey;
    border-radius: 5px;
    cursor: pointer;
    
    transition: background-color 300ms cubic-bezier(0.4, 0, 0.2, 1)
}

.button:hover {
    background-color: #ffa812;
}

.wrapper {
    display: flex;
    margin-top: 20px;
}

.box {
    margin-left: 20px;
}

.genres {
    display: inline-block;
    margin-right: 20px;
}


.link {
        display: inline-block;
            margin-right: 40px;
            margin-bottom: 30px;
            padding: 10px;
        
            font-size: 20px;
            color: #212121;
            background-color: #f5f4fa;
            text-decoration: none;
            border: 1px solid grey;
            border-radius: 5px;
        
            transition: background-color 300ms cubic-bezier(0.4, 0, 0.2, 1)
}

.link:hover{
    background-color: #ffa812;
}